








































































































































































































































































































































































































































































.ifilter-condition-box{
  padding: 20px 20px 20px 20px;
}
.ifilter-condition-container{
  position: relative;
  .icc-condition-box{
    padding-left: 100px;
    padding-right: 50px;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    margin-top: 15px;
    .icc-condition-item{
      padding: 3px 10px;
      margin-right: 5px;
      margin-bottom: 5px;
      transition: all linear 0.3s;
      cursor: pointer;
      &:first-child{
        color: #999999;
        cursor: default;
        position: absolute;
        left: 0;
        padding-left: 0;
        max-width: 96px;
      }
      &.active,&:not(:first-child):hover{
        background-color: #E9F2FA;
        color: #40A9FF;
        border-radius: 4px;
      }
    }
    .icc-condition-item-more{
      position: absolute;
      right: 0px;
      top: 3px;
      svg{
        fill: currentColor;
        width: 12px;
        max-height: 12px;
        vertical-align: -1px;
      }
    }
  }
  .icc-condition-box-more{
    text-align: center;
    margin-top: 10px;
    svg{
      fill: currentColor;
      width: 12px;
      max-height: 12px;
      vertical-align: -1px;
    }
  }
  .icc-check-box{
    display: flex;
    justify-content: space-between;
    .icc-check-condition{
      display: flex;
      justify-content:flex-start;
      &>div{
        margin-right: 10px;
      }
      .icc-check-condition-title{
        svg{
          fill: currentColor;
          width: 12px;
          max-height: 12px;
          vertical-align: -1px;
        }
      }
    }
  }
}
