







































































































































































































































































.scaffold-download-container{
    .sdc-topbox{
        display: flex;
        justify-content: space-between;
        .sdc-title{
            font-weight: bold;
        }
    }
    .sdc-content-box{
      display: flex;
      justify-content: space-around;
      text-align: center;
      margin-top: 20px;
      img{
        max-width: 60px;
      }
      .sdc-item-title{
        font-weight: bold;
        margin-top: 5px;
      }
      .sdc-item-remark{
        font-size: 12px;
        color: #999;
        margin-top: 5px;
      }
      .sdc-item-logo{
        height: 60px;
      }
      .sdc-item-down{
        margin-top: 10px;
      }
    }
}
