    .scc-title{
        font-weight: bold;
    }
    .scc-content{
        margin-top: 5px;
        display: flex;
        justify-content: space-between;
        // flex-direction: ;
        &>div{
            flex-grow: 2;
            flex-shrink:0;
            padding:15px 20px;
            text-align: center;
            position: relative;
            &::after{
                content: ' ';
                position: absolute;
                right: 0px;
                top: 0px;
                bottom: 0px;
                width: 1px;
                background-color: rgba($color: #000000, $alpha: 0.1);
            }
            &:last-child{
                &::after{
                    display: none;
                }
            }
        }
        .scc-item-number{
            font-size: 24px;
            color: #40A9FF;
            span{
                font-size: 14px;
                color: #999999;
            }
        }
    }
}
