    .clc-header-box{
        display: flex;
        justify-content: space-between;
    }
    .clc-list-box{
      .clc-list-item{
        padding: 20px 0px;
        border-bottom: 1px solid #E8E8E8;
        .clc-list-item-header{
          display: flex;
          justify-content: space-between;
          label{
            font-size: 18px;
            font-weight: bold;
            margin-right: 10px;
          }
          font-size: 16px;
        }
        .clc-list-item-desc{
          padding: 15px 0px;
          &.no-content-font{
            color: #ccc;
          }
        }
        .clc-list-item-footer{
          color: #999;
          display: flex;
          justify-content: space-between;
          font-size: 14px;
          user-select: none;
          .clc-list-item-footer-tjbtn{
            cursor: pointer;
          }
        }
        .clc-list-subitem-box{
          padding:0px 20px 20px;
          margin-top: 20px;
          background-color: rgba(0,0,0,0.03);
          .ant-tabs-bar{
            margin: 0 0 0px 0;
          }
          .clc-loadmore-list{
            max-height: 400px;
            overflow: auto;
          }
        }
      }
    }
    .clc-load-more-box{
      text-align: center;
      margin-top: 20px;
      color: #aaaaaa;
    }
}
