































































































































































































































.version-info-container{
    .vic-topbox{
        display: flex;
        justify-content: space-between;
        .vic-title{
            font-weight: bold;
        }
    }
    .vic-content-box{
      display: flex;
      justify-content: space-around;
      text-align: center;
      margin-top: 20px;
      .vic-item-title{
        margin-top: 5px;
        font-size: 36px;
      }
      .vic-item-remark{
        font-size: 14px;
        color: #666;
        margin-top: 5px;
      }
    }
}
