  display: flex;
  flex-wrap: wrap;
}
.component-card-item-box{
  background-color: #ffffff;
  border-radius: 5px;
  width: 285px;
  transition: all linear 0.3s;
  .cc-item-img-container{
    position: relative;
    height: 210px;
    width: 100%;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    overflow: hidden;
    img{
      // max-width: 100%;
      // min-height: 100%;
    }
    .cc-item-img-shade{
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      background-color: rgba(0,0,0,0.41);
      opacity: 0;
      transition: all linear 0.3s;
    }
    .cc-item-img-count-box{
      position: absolute;
      right: 10px;
      bottom: 10px;
      background-color: rgba(0,0,0,0.45);
      color: white;
      height: 24px;
      line-height: 24px;
      border-radius: 12px;
      padding: 0px 12px;
      z-index: 2;
      cursor: pointer;
      svg{
        fill: currentColor;
        width: 16px;
        max-height: 16px;
        vertical-align: -3px;
      }
    }
    .cc-item-img-preview-button{
      position: absolute;
      left: 50%;
      top: 50%;
      width: 90px;
      height: 24px;
      line-height: 22px;
      border-radius: 11px;
      text-align: center;
      color: rgba(255,255,255,0.85);
      border:1px solid rgba(255,255,255,0.85);
      z-index: 2;
      margin-left: -45px;
      margin-top: -12px;
      cursor: pointer;
      transition: all linear 0.3s;
      opacity: 0;
      transition: all linear 0.3s;
      // &:hover{
      //   background: white;
      //   color: #333;
      // }
    }
    .cc-item-img-source-name,.cc-item-img-delete-btn{
      position: absolute;
      top: 0px;
      color: white;
      font-size: 12px;
      padding: 3px 10px;
      z-index: 2;
      cursor: pointer;
      transition: all linear 0.3s;
    }
    .cc-item-img-source-name{
      left: 0px;
      background-color: rgba(64,169,255,0.7);
      &:hover{
        background-color: rgba(64,169,255,0.9);
      }
      border-bottom-right-radius: 5px;
    }
    .cc-item-img-delete-btn{
      display: none;
      right: 0px;
      background-color: rgba(102,102,102,0.4);
      &:hover{
        background-color: rgba(102,102,102,0.6);
      }
      border-bottom-left-radius: 5px;
    }
  }
  .cc-item-info-container{
    // height: 120px;
    border-top: 1px solid #F2F2F2;
    padding: 10px 16px;
    .cc-item-info-title{
      display: flex;
      justify-content: space-between;
      line-height: 33px;
      .cc-item-info-title-comname{
        font-size: 16px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        padding-right: 20px;
        position: relative;
        margin-right: 10px;
        font-weight: bold;
        svg{
          cursor: pointer;
          position: absolute;
          right: 0px;
          top: 50%;
          margin-top: -8px;
          color: #cccccc;
          fill: currentColor;
          width: 16px;
          max-height: 16px;
          vertical-align: -3px;
        }
      }
      .cc-item-info-title-version{
        font-size: 12px;
        color: #40A9FF;
      }
    }
    .cc-item-info-subtitle{
      font-size: 14px;
      color: #666;
      height: 42px;
      overflow: hidden;
      // padding:5px 0px;
    }
    .cc-item-info-tags{
      line-height: 33px;
    }
    .cc-item-info-classid{
      display: flex;
      justify-content: space-between;
      line-height: 33px;
      .cc-item-info-classid-font{
        color: #999;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        span{
          color: #40A9FF;
        }
      }
      .ant-btn-round.ant-btn-sm{
        height: 22px;
        padding: 0 10px;
        font-size: 12px;
        line-height: 21px;
        border-radius: 22px;
      }
    }
  }
  .cc-item-info-button{
    display: none;
    button{
      margin-left: 6px;
    }
  }
  &:hover{
    box-shadow: 0px 0px 15px rgba(0,0,0,0.15);
    .cc-item-info-button,.cc-item-img-shade,.cc-item-img-preview-button,.cc-item-img-delete-btn{
      display: block;
      opacity: 1;
    }
  }
}
.cc-item-img-box{
  width: 800px;
  height: 500px;
  overflow: auto;
  text-align: center;
  img{
    max-width: 100%;
    margin-bottom: 5px;
  }
  p{
    margin-bottom: 10px;
  }
}
