  display: flex;
  justify-content:space-between;
  height: 60px;
  box-shadow: 0px 2px 8px rgba(0,0,0,0.1);
  line-height: 60px;
  .nc-logo{
    white-space: nowrap;
    width: 25%;
    padding: 0px 20px;
    img{
      width: 36px;
    }
    span{
      font-size: 18px;
      font-weight: bold;
      margin-left: 10px;
      position: relative;
      label{
        color: red;
        position: absolute;
        top: -30px;
        right: -30px;
        font-size: 14px;
        font-weight: 400;
      }
    }
  }
  .nc-menu{
    width: 50%;
    display: flex;
    justify-content:center;
    font-size: 16px;
    white-space: nowrap;
    
    >div{
      padding:0px 18px;
      position: relative;
      cursor: pointer;
      &:hover{
        color: #40A9FF;
      }
    }
    >div.active{
      color: #40A9FF;
      &::before{
        content: ' ';
        position: absolute;
        bottom: 0px;
        height: 2px;
        width: 100%;
        left: 0px;
        background-color: #40A9FF;
      }
    }
  }
  .nc-user{
    width: 25%;
    padding: 0px 20px;
    display: flex;
    justify-content: flex-end;
    align-items:center;
    .user-info{
      margin-left: 20px;
      font-size: 14px;
    }
  }
}
